.full_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
body {
  width: 100vw;
  overflow-x: hidden;
}
.sidebar {
  position: fixed;
  width: 37vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  top: 0;
  right: -37vw;
  z-index: 20;
  -webkit-transition: 0.35s;
  transition: 0.35s;
  color: white;
  display: flex;
  flex-direction: column;
}
.sidebar_title {
  margin-top: 80px;
  font-family: "Titillium Web", "san-selif";
  font-weight: 900;
  align-self: center;
  font-size: 40px;
  padding-top: 40px;
}
.sidebar_list {
  font-family: "escore9";
  color: rgb(153, 153, 153);
  font-size: 20px;
  text-decoration: underline;
}
.sidebar_list > li {
  cursor: pointer;
  margin-bottom: 10px;
}
@media (max-width: 1070px) {
  .sidebar {
    width: 50vw;
    right: -50vw;
  }
  .sidebar_list {
    font-size: 40px;
  }
}
@media (max-width: 770px) {
  .sidebar {
    width: 100vw;
    right: -100vw;
  }
  .sidebar_title {
    padding-bottom: 20px;
  }
  .sidebar_list {
    font-size: 30px;
  }
  .sidebar_list > li {
    margin-bottom: 20px;
  }
}
.menu_container {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  padding-bottom: 30px;
  position: fixed;
  width: 100vw;
  z-index: 30;
  background: white;
  top: 0;
  box-shadow: 1px 1px black;
  position: relative;
}
.menu_home {
  margin-left: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.menu_now {
  display: flex;
  align-items: center;
  font-family: "Titillium Web", "san-selif";
  font-weight: 900;
  color: rgba(96, 74, 138);
  font-size: 16px;
  -webkit-transition: 1s;
  transition: 1s;
}
.menu_menu {
  margin-right: 40px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
@media (max-width: 770px) {
  .menu_container {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .menu_home {
    margin-left: 30px;
  }
  .menu_menu {
    margin-right: 30px;
  }
}

